/* CSS Styling for index.js */

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  .site-container {
    min-height: 100vh;
  }
  
  main {
    flex: 1 1;
  }

  .ArticleHeader {
    margin: 1rem;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: #e36414;
    border-radius: 25px;
    text-align: center;
  }
/* CSS Styling for App.js */
.App {
  text-align: center;
}

.header {
  margin: 1rem;
  background-color: #e36414;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  font-size: 2rem;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  color: white;
  border-radius: 25px;
}

.footer{
  background-color: #0f4c5c;
  text-align: center;
  color: white;
  padding: 0.5rem;
  margin: 1rem;
  border-radius: 25px;
}



/* CSS Styling for AboutBox.js */

.Card{
  margin-left: 1rem;
  padding: 1rem;
  width: 60rem;
  border-radius: 25px;
  border: 2px solid #0f4c5c;
  box-sizing: border-box;
}

.linkDiv{
  display: flex;
  justify-content: space-between;
  text-decoration: none;
  padding-bottom: 1rem;

}



/* CSS Styling for Article.js */

.Card{
    margin: 1rem;
    padding: 1rem;
    width: 60rem;
    border-radius: 25px;
    border: 2px solid #0f4c5c;
    box-sizing: border-box;
    margin-left: auto;
    margin-right: auto;
}

.BackCard {
    background-color: #e36414;
    padding: 1rem;
    margin: 3rem;
    width: 65rem;
    margin-left: auto;
    margin-right: auto;
    border-radius: 25px; 
}

.VideoPill {
    border-radius: 1.5rem;
    line-height: 1.5rem;
    font-size: 1rem;
    color: white;
    background-color: #e36414;
    padding: 0.25rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    box-sizing: content-box;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 1.5rem;
    text-decoration: none;  
}

.VideoPill:hover {
    cursor: pointer;
}
